<template>
	<view class="view">
		<view class="title">物流信息</view>
		<view class="middle">
			<text class="title-name">物流公司：</text>
			<text class="number">{{order.LogisticsCompany}}</text>
		</view>
		<view class="middle last">
			<text class="title-name">物流单号：</text>
			<text class="number">{{order.LogisticsNo}}</text>
			<text class="copy" @click="handleCopy">复制</text>
		</view>
		<view class="btn" @click="hide">关闭</view>
	</view>
</template>

<script>
	export default {
		props: {
			order: {
				default: {}
			}
		},
		data() {
			return {
				number: 'SF1234567890'
			}
		},
		methods: {
			hide() {
				this.$emit("onHide", false)
			},
			/**
			 * 复制物流单号
			 */
			handleCopy() {
				uni.setClipboardData({
					data: this.order.LogisticsNo, //要被复制的内容
					success: () => { //复制成功的回调函数
						uni.showToast({ //提示
							icon: 'none',
							title: '复制成功'
						})
					}
				});
			}
		}
	}
</script>

<style scoped lang="scss">
	.view {
		text-align: left;
		// margin: 362rpx 90rpx 0;
		background: #fff;
		line-height: 40rpx;
		font-size: 28rpx;
		font-weight: 400;
		border-radius: 16rpx;
	}

	.title {
		font-size: 32rpx;
		font-weight: 500;
		color: #333333;
		text-align: center;
		line-height: 44rpx;
		padding: 30rpx 0;
	}

	.middle {
		padding: 0 40rpx 20rpx;
	}

	.last {
		padding-bottom: 30rpx;
		border-bottom: 2rpx solid #efefef;
	}

	.title-name {
		color: #999999;
	}

	.number {
		color: #333333;
	}

	.copy {
		padding-left: 20rpx;
		color: #00D08F;
	}

	.btn {
		text-align: center;
		margin: 30rpx;
		padding-bottom: 30rpx;
		color: #999999;
	}
</style>
